import React from 'react';
import Shuffle from './Shuffle';


class Card extends React.Component{
    constructor(){
        super()
        this.state ={
            input: '',
            validInput : false,
            error : false,
            errorData : ''
        }

        this.getPlayer = this.getPlayer.bind(this)
        
    }

    getPlayer(){
        const regexNumber = /^[1-9]/
        if( !regexNumber.test(this.playerInput.value) ){
            this.setState({
                error: true,
                errorData : 'Input value does not exist or value is invalid'
            })
        }else{
            if( this.state.error ){
                this.setState({
                    error: false,
                    errorData : ''
                })
            }
            this.setState({
                input : this.playerInput.value,
                validInput : true
            })
        }    
    }
    render(){
        const ifValidInput = <h5>Number of players : {this.state.input}</h5>
        const ifError = <h5 style = {{color: "red"}}>{this.state.errorData}</h5>
        return (
            <div>
              <h3>Enter Number of Players</h3>
                <input className = 'input-name' type="number" ref={(c) => this.playerInput = c}  required />
                { (this.state.error)? ifError  : ifError }
                <br/>
                <button
                    type="button" onClick={this.getPlayer} value = 'submit'
                >Submit</button>
              {(this.state.validInput) ? ifValidInput  : null}
              { (this.state.validInput) ? <Shuffle numberOfPlayer = {this.state.input} /> : null }
            </div>
        );
    }

}

export default Card