import React from 'react';
import axios from 'axios';
const API_PATH = '/api/index.php';

class Shuffle extends React.Component{
    constructor(){
        super()
        this.state = {
            data: [],
            error : false
        }
    }

    componentDidMount(){
        axios({
            method: 'post',
            url: `${API_PATH}`,
            headers: { 'content-type': 'application/json' },
            data: { player : this.props.numberOfPlayer }
          })
            .then(result => {
            
              console.log(result);
              this.setState({
                  data: result.data
              })
              
              if( this.state.error === true ){
                    this.setState({
                        error: false
                    })
              }
            })
            .catch(error => {
                console.log(error) 
                console.log(error.status) 
                this.setState({
                    error: true

                })
            });
    }

    render(){
        const validData = <div> {this.state.data.map((getData) => ( <div key = {getData.id} className="card"><p>{getData.data}</p> </div>))} </div>

        return(
            <div>
                { ( !this.state.error ) ? validData : null }
            </div>
        )
    }
}

export default Shuffle